.App {
    text-align: center;
}
.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    color: rgb(22, 21, 21);
    font-family: "Times New Roman", Times, serif;
    font-size: 50 px;
    font-weight: bold;
}
.App-logo {
    margin: auto 5px;
    float: left;
    width: 220px;
    height: 210px;
}

input {
    border: 3px groove black;
    width: 20%;
    font-size: 20px;
}
span {
    color: rgb(179, 20, 20);
    font-weight: bold;
    font-size: 30px;
    font-family: "Times New Roman", Times, serif;
}
.App-body {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    color: rgb(22, 21, 21);
    font-family: "Times New Roman", Times, serif;
    /* background-color: hsl(0, 79%, 40%); */
}

.App-header {
    background-color: rgb(179, 20, 20);
    height: 200px;
    padding: 20px;
    color: #222;
}

.App-title {
    font-size: 35px;
    font-weight: bold;
    color: white;
}

cite {
    font-weight: bold;
}
table {
    align-content: center;
    margin: auto;
}
td {
    padding: 10px;
}

.App-intro {
    font-size: large;
}
.cap-first-letter:first-letter {
    text-transform: capitalize;
}
.col-sm-6,
col-md-4 {
    border: 1px;
}

.hometext {
    max-width: 60%;
}

li {
    padding: 20px;
}

p {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

div {
    text-align: center;
}

a {
    color: black;
    text-decoration: none !important;
    font-size: 30px;
    font-weight: bold;
}

li a:hover {
    background-color: transparent;
    color: blue;
}

ul {
    list-style: none;
}

.title {
    font-weight: bold;
    color: rgb(179, 20, 20);
    font-size: 30px;
}
.title1 {
    font-weight: bold;
    color: rgb(179, 20, 20);
    font-size: 40px;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
    input {
        font-size: 20px;
    }
}

dt,
dl {
    padding: 2px;
    margin-top: 0px;
    margin-bottom: 0px;
    font-weight: bold;
}

dt {
    display: inline-block;
    font-size: 30px;
}
dl {
    font-size: 20px;
}
dd {
    display: inline;
    margin-left: 5px;
    font-size: 30px;
    color: black;
}

.button1 {
    font-size: medium;
    color: rgb(179, 20, 20);
    font-weight: bold;
    text-decoration: none !important;
    border: grey groove 2px;
    padding: 10px;
    background-color: whitesmoke;
    margin-top: 15px;
}

.pagecontent {
    text-decoration: none !important;
    background: #b71540;
    border-radius: 50%;
    border: black solid 3px;
    padding: 10px;
    color: white;
}

.pagecontent:hover {
    /* color:#FFCB05; */
    background-color: transparent;
    color: #b71540;
    border: 2px solid black;
}
.showlink {
    padding: 0.4rem 0.8rem;
    background-color: white;
    border-radius: 3px;
    color: #222;
    text-decoration: none !important;
    margin: 5px;
}

.showlink:hover {
    background-color: transparent;
    color: white;
    border: 2px solid white;
}

.button {
    padding: 14px 25px;
    background-color: rgb(156, 52, 52);
    border-radius: 3px;
    color: white;
    text-decoration: none !important;
    font-weight: bold;
    font-size: 30px;
}

